	module.exports = {"data":{"markdownRemark":{"html":"<p>Web development is a broad term for the work involved in developing a web site for the Internet (World Wide Web) or an intranet (a private network). Web development can range from developing the simplest static single page of plain text to the most complex web-based internet applications (or just 'web apps') electronic businesses, and social network services. A more comprehensive list of tasks to which web development commonly refers, may include web engineering, web design, web content development, client liaison, client-side/server-side scripting, web server and network security configuration, and e-commerce development.</p>","frontmatter":{"title":"Basic Web Development","author":"Barry Allen","date":"2018-01-01"}}},"pathContext":{"slug":"/blog-posts/basic-web-dev/"}}

